<template>
   <div class="welcome-text">
      <h2 class="welcome-title">I Am The
         <span class="welcome-title__typed-text" :class="{'typing': typeStatus}">{{ typeValue }}</span>
      </h2>
      <h2 class="welcome__sub-title">Welcome To My Website</h2>
      <a class="welcome__button" href="#" type="button">Get Started</a>
   </div>
</template>

<script>
   export default {
      name: "WelcomeText",
      data() {
         return {
            typeValue: '',
            typeStatus: false,
            typeArray: ['Developer', 'Designer', 'Creator'],
            typingSpeed: 200,
            erasingSpeed: 100,
            newTextDelay: 3000,
            typeArrayIndex: 0,
            charIndex: 0
         }
      },
      methods: {
         typeText() {
            if(this.charIndex < this.typeArray[this.typeArrayIndex].length) {
               if(!this.typeStatus)
                  this.typeStatus = true;

               this.typeValue += this.typeArray[this.typeArrayIndex].charAt(this.charIndex);
               this.charIndex += 1;

               setTimeout(this.typeText, this.typingSpeed);
            } else {
               this.typeStatus = false;
               setTimeout(this.eraseText, this.newTextDelay)
            }
         },
         eraseText() {
            if(this.charIndex > 0) {
               if(!this.typeStatus)
                  this.typeStatus = true;

               this.typeValue = this.typeArray[this.typeArrayIndex].substring(0, this. charIndex - 1);
               this.charIndex -= 1;
               setTimeout(this.eraseText, this.erasingSpeed);
            } else {
               this.typeStatus = false;
               this.typeArrayIndex += 1;
               if(this.typeArrayIndex >= this.typeArray.length)
                  this.typeArrayIndex = 0;

               setTimeout(this.typeText, this.typingSpeed + 1000);
            }
         }
      },
      created() {
         setTimeout(this.typeText, this.newTextDelay + 200);
      }

   }
</script>

<style lang="scss">
   .welcome{

      &-text{
         width: 100%;
         height: calc(100% - 80px);
         overflow: hidden;
         display: flex;
         flex-direction: column;
         justify-content: center;
         padding: 0 40px;
         color: #fff;
      }

      &-title{
         font-size: 90px;
         font-weight: 400;
         letter-spacing: 4px;
         margin: 0 0 35px 0;

         &__typed{

            &-text{
               color: #E8D110;
               border-right: 4px solid #fff;
               animation: cursorBlink 1s infinite;
            }

            &-cursor{
               display: inline-block;
               margin: 0 0 0 4px;
               width: 4px;
            }
         }
      }

      &__sub-title{
         font-size: 45px;
         color: #E0DEDE;
         margin-bottom: 45px;
      }

      &__button{
         position: relative;
         width: 200px;
         height: 60px;
         text-align: center;
         line-height: 60px;
         color: #fff;
         font-size: 24px;
         background: linear-gradient(90deg, #03a9f4, #f441a4, #ffeb3b, #03a9f4);
         background-size: 400%;
         border-radius: 30px;
         z-index: 1;

         &:hover{
            animation: animate-button 8s linear infinite;
         }

         &:before{
            content: '';
            position: absolute;
            top: -5px;
            bottom: -5px;
            right: -5px;
            left: -5px;
            z-index: -1;
            background: linear-gradient(90deg, #03a9f4, #f441a4, #ffeb3b, #03a9f4);
            background-size: 400%;
            border-radius: 40px;
            filter: blur(20px);
            opacity: 0;
            transition: 0.5s;
         }

         &:hover:before{
            filter: blur(20px);
            opacity: 1;
            animation: animate-button 8s linear infinite;
         }
      }
   }

   .typing{
      animation: none;
   }

   @keyframes cursorBlink {
      49% { border-right: 4px solid #fff; }
      50% { border-right: 4px solid transparent; }
      99% { border-right: 4px solid transparent; }
   }

   @keyframes animate-button {
      0% {
         background-position: 0;
      }
      100% {
         background-position: 400%;
      }
   }
</style>