<template>
   <div class="burger-menu__wrap">
      <input type="checkbox" class="burger-menu__toggler">
      <div class="burger-menu__hamburger"><div></div></div>
      <div class="menu">
         <div>
            <div>
               <ul>
                  <li><a href="#">Skills</a></li>
                  <li><a href="#">Projects</a></li>
                  <li><a href="#">Experience</a></li>
               </ul>
            </div>
         </div>
      </div>
   </div>
</template>

<script>
   export default {
      name: "BurgerMenu"
   }
</script>

<style lang="scss">
   .burger-menu{

      &__wrap{
         position: fixed;
         top: 25px;
         left: 25px;
         z-index: 1;
         display: none;
      }

      &__wrap &__toggler{
         position: absolute;
         top: 5px;
         left: 5px;
         z-index: 2;
         cursor: pointer;
         width: 50px;
         height: 50px;
         opacity: 0;
         margin: 0;
      }

      &__wrap &__hamburger{
         position: absolute;
         top: 0;
         left: 0;
         z-index: 1;
         width: 55px;
         height: 60px;
         display: flex;
         align-items: center;
         justify-content: center;
      }

      //Hamburger line
      &__wrap &__hamburger > div{
         position: relative;
         width: 100%;
         height: 3px;
         background: #fff;
         display: flex;
         align-items: center;
         justify-content: center;
         transition: all .4s ease;
         border-radius: 5px;
      }

      //Top and bottom lines
      &__wrap &__hamburger > div:before,
      &__wrap &__hamburger > div:after{
         content: '';
         position: absolute;
         z-index: 1;
         top: -18px;
         width: 100%;
         height: 3px;
         background: inherit;
         border-radius: 5px;
      }

      //Moves line down
      &__wrap &__hamburger > div:after{
         top: 18px;
      }

      //Toggler animate
      &__wrap &__toggler:checked + &__hamburger > div{
         transform: rotate(315deg);
      }

      //Turn lines into X
      &__wrap &__toggler:checked + &__hamburger > div:before,
      &__wrap &__toggler:checked + &__hamburger > div:after{
         top: 0;
         transform: rotate(90deg);
      }

      //Rotate on hover when checked
      &__wrap &__toggler:checked:hover + &__hamburger > div{
         transform: rotate(225deg);
      }

      //Show menu
      &__wrap &__toggler:checked ~ .menu{
         visibility: visible;
      }

      &__wrap &__toggler:checked ~ .menu > div{
         transform: scale(1);
         transition-duration: .75s;
      }

      &__wrap &__toggler:checked ~ .menu > div > div{
         opacity: 1;
         transition: opacity .4s ease;
      }


      &__wrap .menu{
         position: fixed;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         visibility: hidden;
         overflow: hidden;
         display: flex;
         align-items: center;
         justify-content: center;
      }

      &__wrap .menu > div{
         background: rgba(57, 55, 57, 0.75);
         border-radius: 50%;
         width: 200vw;
         height: 200vw;
         display: flex;
         flex: none;
         align-items: center;
         justify-content: center;
         transform: scale(0);
         transition: all .4s ease;
      }

      &__wrap .menu > div > div{
         text-align: center;
         max-width: 90vw;
         max-height: 100vh;
         opacity: 0;
         transition: opacity .4s ease;
      }

      &__wrap .menu > div > div > ul > li{
         color: #fff;
         font-size: 1.5rem;
         padding: 1rem;
      }

      &__wrap .menu > div > div > ul > li > a{
         color: inherit;
         transition: color .4s ease;
      }
   }

</style>