<template>
   <section v-if="show" class="pre-loader">
      <div class="container">
         <div class="loader">
            <span></span>
         </div>
      </div>
   </section>
</template>

<script>
   export default {
      name: "PreLoader",
      data() {
         return{
            show: true
         }
      },
      mounted() {
         this.showToggle();
      },
      methods: {
         showToggle() {
            setTimeout(() => {
               this.show = false;
            }, 3000)
         }
      }
   }
</script>

<style lang="scss">
   .pre-loader{
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 100vh;
      background: #E56B0A;
      position: absolute;
      width: 100%;
      z-index: 9999;
   }

   .container{
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      -webkit-box-reflect: below 1px linear-gradient(#0001, #0004);

      & .loader{
         position: relative;
         width: 200px;
         height: 200px;
         border-radius: 50%;
         background: #CE3911;
         animation: animate 2s linear infinite;

         &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 50%;
            height: 100%;
            background-size: 100px 180px;
            background: linear-gradient(to top, transparent, rgba(243, 243, 7, .4)) no-repeat;
            border-top-left-radius: 100px;
            border-bottom-left-radius: 100px;
         }

         &:after{
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 20px;
            height: 20px;
            background: #E8D110;
            border-radius: 50%;
            z-index: 10;
            box-shadow: 0 0 10px #E8D110,
                        0 0 20px #E8D110,
                        0 0 30px #E8D110,
                        0 0 40px #E8D110,
                        0 0 50px #E8D110,
                        0 0 60px #E8D110,
                        0 0 70px #E8D110,
                        0 0 80px #E8D110,
                        0 0 90px #E8D110,
                        0 0 100px #E8D110;
         }

         & span{
            position: absolute;
            top: 20px;
            left: 20px;
            right: 20px;
            bottom: 20px;
            background: #E56B0A;
            border-radius: 50%;
         }
      }
   }

   @keyframes animate {
      0%{
         transform: rotate(0deg);
      }
      100%{
         transform: rotate(360deg);
      }
   }

</style>